@import url(https://fonts.googleapis.com/css?family=Andika&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
	outline: none;
}

body {
	font-family: Andika, sans-serif;
	background: #e5e9ec;
}

.App {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
}

.themeChanger {
	display: flex;
	flex-direction: column;
}

.ActionButton {
	border: none;
	background-color: #fdd01c;
	box-shadow: 0px 7px 0px #caa616;
	padding: 30px 45px;
	font-size: 25px;
	width: 240px;
	cursor: pointer;
}
.ActionButton:active {
	box-shadow: 0px 1px 0px #fdd01c;
}
.ActionButton:hover {
	background-color: #f0c51a;
}

.InfoField {
	min-height: 350px;
	font-size: 20px;
	min-width: 240px;
	max-width: 600px;
	margin: 50px 20px 10px;
	text-align: center;
	font-weight: 600;
}

.InfoFieldLong {
	font-size: 17px;
}

@media (min-width: 480px) {
	.InfoField {
		font-size: 25px;
	}
	.InfoFieldLong {
		font-size: 19px;
	}
}

@media (min-width: 768px) {
	.InfoField {
		font-size: 30px;
	}
	.InfoFieldLong {
		font-size: 22px;
	}
}

